.header__toggle-icon {
  position: relative;
  width: 100%;
  height: 4px;
  background-color: #fff;
}

.header__toggle-icon:before, .header__toggle-icon:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  left: 0;
  background-color: #fff;
  transition: transform .3s;
}

.header__toggle-icon:before {
  top: -12px;
}

.header__toggle-icon:after {
  bottom: -12px;
}

.header__toggle-icon.is-open {
  background-color: transparent;
}

.header__toggle-icon.is-open:before {
  transform: rotate(45deg);
  top: 0;
  background-color: #fff;
}

.header__toggle-icon.is-open:after {
  transform: rotate(-45deg);
  bottom: 0;
  background-color: #fff;
}

